<template>
  <div>
    <div class="row">
        <div class="col-12 p-4 bg-white">
            <div class="p-2 float-left" @click="$router.push('/')">
                <span class="shadow-lg p-2 hover:bg-red-400"><span class="typcn typcn-arrow-left"></span></span>
            </div>
            <div style="height:200px;">

            </div>
            <p class="text-center p-2 font-bold font-times font-italic">PROFILE</p>
            <hr class="style13">
            <div class="text-center ">
                <span class="p-3 shadow-lg" style=""><span class="typcn typcn-user"></span></span>
            </div>
            <div class="p-3 shadow-lg rounded-lg">
                <div class="sm-form ">
                    <label for="nama">Nama</label>
                    <input type="text" id="nama" name="nama" class="form-control form-control-sm" placeholder="nama" v-model="vdata.nama" >
                </div>
                <div class="sm-form ">
                    <label for="hp">No HP/WA : </label>
                    <input type="number" id="hp" name="hp" class="form-control form-control-sm" placeholder="hp" v-model="vdata.hp" >
                </div>
                <hr class="style13">
                <div class="p-3">
                    <button type="button" @click="simpan" class="btn btn-sm btn-dark  ">Edit <span class="typcn typcn-edit"></span></button>
                    <br>
                    <p class="font-bold p-2 ">Data ini dibutuhkan untuk memudahkan dalam pengiriman barang !</p>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
// let rdb = firebase.database()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
        vdata:{},
        datanya:{}
    }
  },
  methods:{
      simpan(){
          db.collection(`users`).doc(this.$store.state.users.uid).set(this.vdata,{
              merge:true
          }).then(res=>{
              alert("Berhasil di simpan!")
              db.collection(`users`).doc(this.$store.state.users.uid).get().then(res=>{
                  this.$store.commit('changeUsers',{
                      ...res.data()
                  })
              })
              this.$router.push('/')
          })
      }
  },
  layout:"admin",
  mounted() {
    db.collection(`users`).doc(this.$store.state.users.uid).onSnapshot(res=>{
        this.datanya = res.data()
        this.vdata = res.data()
        this.$forceUpdate()
    })
  },
  metaInfo() {
      return {
        title: "Tentang TA_Maliki / Taufik Akbar Maliki",
        meta: [{  hid: "description",  name: "description",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{  property: "og:title",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{ property: "og:site_name", content: "TA_Maliki" },{ property: "og:type", content: "website" },{  property: `og:url`,  content: `${this.$store.state.url}`,},{  property: `og:image`,  content: `${this.$store.state.url}/Kino.jpg`,},{  property: `og:description`,  content: `documentasi ya`,},{  name: "keywords",  content: "blog,portofolio,freelance",},{ name: "robots", content: "index,follow" },],
      };
}}
</script>